import React, {useState} from 'react';
import './App.css';
import {movies} from './movies';


function App(){

  return (
    <div>
       <Movie/>
    </div>
   
  );
}

function Movie() {
  const [currentMovie, setCurrentMovie]  = React.useState(null) //null is the initial state.. this is the only state we can pass as an argument.
  
//console.log(currentMovie);
  
  
  return (
  <div className="movieStyleDiv">
      <h1>movie Information </h1>
        {
          movies.map((movie) => {

          return (
            
           <button  style={{margin: '20'}} key={movie.title} onClick={() => {setCurrentMovie(movie)}}>
           <img width="100px" src={movie.posterUrl} alt="img" />  
          <p style={{margin: "20px",fontWeight:"bold"}}>{movie.title}</p>
    
          </button>
       
             )
          }
        )
      }
      
      {
        currentMovie ?
        
        <div style={{textAlign: "center", margin:"5%"}}>
          <img width="200px" src={currentMovie.posterUrl} alt="img"/>
          <p>{currentMovie.title}</p>
           <p>{currentMovie.rating}</p>
           <p>{currentMovie.director}</p>
           <p>{currentMovie.releaseYear}</p>
           <p>{currentMovie.categories.join(' , ')}</p>
           
          </div>
          :
          <p>Please Select a Movie!!</p>
      }
    </div>
  );
}





export default App;
